.flexContainer {
  display: flex;
  height: 100vh;
  background-color: #f0f0f0;
}

.sidebar {
  width: 250px;
  background-color: #266ca9;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.logoContainer {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.logo {
  width: 80px;
  height: 80px;
  margin-right: 10px;
}

.collegeName {
  font-size: 22px;
  font-weight: bold;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.whiteButton, .ghostButton {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 4px;
  background-color: transparent;
  color: white;
  text-align: left;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.whiteButton {
  background-color: white;
  color: #266CA9;
  transition: background-color 0.3s;
}

.whiteButton:hover, .ghostButton:hover {
  background-color: #46acf5;
  color: #266CA9;
}

.navIcon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.mainContent {
  flex-grow: 1;
  padding: 20px;
  overflow-y: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.dashboardTitle {
  font-size: 24px;
  color: #333;
}

.headerRight {
  display: flex;
  gap: 12px;
}

.academicYear, .semesterBadge {
  padding: 5px 10px;
  border-radius: 5px;
}

.avatar {
  width: 30px;
  height: 30px;
  background-color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  overflow: hidden;
  margin-top: -5px;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cardGrid {
display: grid;
grid-template-columns: repeat(3, 1fr);
gap: 20px;
margin-bottom: 20px;
}

.card {
background-color: white;
padding: 16px;
border-radius: 8px;
box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.cardHeader {
display: flex;
justify-content: space-between;
align-items: center;
margin-bottom: 3px;
}

.cardHeader2 {
display: flex;
justify-content: space-between;
align-items: center;
margin-bottom: 10px;
}

.cardTitle {
font-size: 16px;
color: #333;
}

.blueIcon, .greenIcon, .yellowIcon {
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.blueIcon {
  background-color: #0000FF;
}

.greenIcon {
  background-color: #008000;
}

.yellowIcon {
  background-color: #FFFF00;
}

.boldText {
  font-size: 24px;
  font-weight: bold;
}

.smallText {
  font-size: 12px;
  color: #666;
}

.modal {
  position: absolute;
  right: 0;
  top: 60px;
  background-color: white;
  border: 1px solid #e1e4e8;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 200px;
  z-index: 1000;
  overflow: hidden;
}

.modal ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.modal ul li {
  padding: 12px 16px;
  cursor: pointer;
  color: #333;
  font-size: 14px;
  line-height: 1.5;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e1e4e8;
}

.modal ul li:last-child {
  border-bottom: none;
}

.modal ul li:hover {
  background-color: #f6f8fa;
}

.modal ul li img {
  margin-right: 8px;
  width: 20px;
  height: 20px;
}